import React, { useEffect, useRef } from 'react';
import Footer from './components/footer/Footer';
import Slide from './components/slide/Slide';
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

function Roadmap() {
    const headline1 = useRef();
    gsap.registerPlugin(ScrollTrigger);
    useEffect(() => {
        headline1.current.style.transform = 'translateX(-100%)';
        gsap.to(headline1.current, {
            x: 0,
            ease: '',
        });
        gsap.from('#text1', {
            duration: 1,
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text1',
                start: 'top 100%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text2', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text2',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text3', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text3',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text4', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text4',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text5', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text5',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text6', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text6',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text7', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text7',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text8', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text8',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text9', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text9',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text10', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text10',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text11', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text11',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text12', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text12',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text13', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text13',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text14', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text14',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text15', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text15',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text16', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text16',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
        gsap.from('#text17', {
            opacity: 0,
            ease: 'none',
            y: 20,
            scrollTrigger: {
                trigger: '#text17',
                start: 'top 80%',
                end: 'bottom bottom',
                scrub: 1,
            },
        });
    }, []);
    return (
        <div>
            <section className="roadmap-box">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-10">
                            <div className="readmap-content">
                                <p>
                                    Our roadmap is designed with one purpose in
                                    mind, to create i/O Underground by mid to
                                    late 2022.
                                </p>
                                <p>
                                    The items below are all subject to review
                                    with our community as we lead up to mint
                                    day. Everything is comfortably achievable
                                    but we appreciate fresh ideas and impetus
                                    from those of you who see the potential for
                                    our end goal. Your idea could lead to us
                                    proposing changes to the below, any changes
                                    will be voted on within the community and
                                    will be subject to review by our project
                                    team before going to vote.
                                </p>
                                <p>
                                    Once the mint has completed, a detailed
                                    roadmap for i/O Underground will be
                                    released.
                                </p>
                                <h2 ref={headline1}>Tesla Giveaway</h2>
                                <p id="text1">
                                    Upon selling out our collection we will be
                                    raffling off a brand new Tesla to one of
                                    you. The lottery will be shown live shortly
                                    after we sell out. Your wallet address will
                                    act as your ticket, and the amount of NFTs
                                    your address owns equals your quantity of
                                    tickets.
                                </p>
                                <p id="text2">
                                    *The Tesla awarded will be subject to the availability
                                    in the winners country. For instance, if the winner
                                    wanted the new Model S, this is not due to ship 
                                    until late 2022, which may not appeal to the winner.
                                    Ultimately, the choice will be yours.
                                </p>
                                <h2 id="text3" >Merch Collab</h2>
                                <p id="text4">
                                    Work is already underway to partner up with
                                    a number of fashion brands the team here has
                                    existing relationships with. The material
                                    and design of each item we release will be
                                    of the highest quality. Announcements will
                                    be made over the course of early 2022 with
                                    products starting to be released by late
                                    spring if not sooner.
                                </p>
                                <h2 id="text5">Headz Companions</h2>
                                <p id="text6">
                                    As is common in most successful NFT
                                    projects, we wish to create a fund that the
                                    community manages to drive the charity and
                                    development of ideas that holders wish to
                                    pursue. To fund this, we plan to release
                                    companions for your headz with all project
                                    royalties and initial mint fees going into
                                    the pot to kick start the fund.
                                </p>
                                <p>
                                    We are looking into the option of implementing this as DAO.
                                </p>
                                <h2 id="text16">1/1 Prints</h2>
                                <p id="text17">
                                    1/1 high res prints will be available soon after the mint. More info will be shared post sale.
                                </p>                                
                                <h2 id="text7">Metaverse Gallery</h2>
                                <p id="text8">
                                    A common wish we are seeing within other
                                    communities and projects is a desire for
                                    virtual gallery spaces. This idea intrigues
                                    us and we are open to launching our own
                                    should the community agree this would be
                                    beneficial to the project. This may be
                                    counter productive given our i/O Underground
                                    concept could be deemed as similar, but it
                                    may present an opportunity to showcase some
                                    of the i/O concept before launch.
                                </p>
                                <h2 id="text9">i/O Underground</h2>
                                <p id="text10">
                                    The most ambitious goal on our roadmap and
                                    the reason we are here in the first place.
                                </p>
                                <p id="text11">
                                    i/O will be where Naked Headz blurs the
                                    lines between metaverse and your physical
                                    surroundings. Again leaning on our teams
                                    existing relationships and experience, we
                                    plan to launch multiple electronic music
                                    events all around the globe that will be
                                    mirrored in a virtual landscape and accessed
                                    anywhere in the world.
                                </p>
                                <p id="text12">
                                    A number of TBA world renowned artists and
                                    DJs have already committed to performances.
                                    These performances will be streamed directly
                                    to a virtual equivalent to be enjoyed by
                                    those who are plugged in. Your NFT will be
                                    your ticket to both universes.
                                </p>
                                <p id="text13">
                                    We are in conversations with a number of
                                    existing metaverse projects + platforms who
                                    are open to our proposal to partner up and
                                    develop the concept over the first half of
                                    2022. The early success of Naked Headz will
                                    of course affect the progress of this
                                    initiative, but we are confident that with a
                                    sell out we can launch a full programme of
                                    events by late summer.
                                </p>
                                <p id="text14">
                                    We plan to also release limited edition runs
                                    of NH NFTs from our artists and DJs, and
                                    early ideas around a token that can be used
                                    within both environments is underway.
                                </p>
                                <p id="text15">
                                    More info will be shared in our upcoming
                                    whitepaper and medium posts
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Slide />
            <Footer />
        </div>
    );
}

export default Roadmap;
